@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
.App {
  text-align: center;
}

* {
  margin: 0;
  padding: 0 10px;
}

.post {
  font-family: Oswald, sans-serif;
  padding: 30px;
  box-shadow: 0 0 5px 0 rgba(0,0,0,0.75);
}

.post__headline {
  font-size: 22px;
  margin-bottom: 30px;
}

.post__article {
  width: 300px;
  margin: 0 auto;
}

.post__content {
  font-size: 15px;
}

.address {
  padding: 5px;
  border: 1px solid #6b6a69;
  margin-top: 20px;
}

.user {
  margin-top: 50px;
}

.user__info {
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.user__contacts {
  margin: 0 auto;
}

.comments__list {
  list-style: none;
  width: 500px;
  float: right;
}

.comments__list_item {
  padding: 20px;
  margin-bottom: 30px;
  border: 2px dotted #6b6a69;
}

.comments__list_content {
  width: 300px;
  margin: 0 auto;
}

.posts__list {
  display: grid;
  margin-top: 20px;
  grid-template-columns: repeat(auto-fill, minmax(800px, 1fr));
  grid-gap: 30px;
}

